
import React, {Component} from 'react';
import Header from './parts/header/Header.js';
import Main from './parts/main/Main.js';
import Footer from './parts/footer/Footer.js';
import {handleUserError, handleDetailMatch} from './utilities/validation/validation';



class App extends Component{
 state = {
   reveal:'fa fa-eye-slash',
   inputType : 'password',
   username:'',
   password:'',
   error : '',
   usernameError : '',
   passwordError: '',

 }
 setPassWordStatus = () => {
   let query = this.state.reveal === 'fa fa-eye-slash';
   this.setState({reveal:`${query ? 'fa fa-eye' : 'fa fa-eye-slash'}`, inputType: `${query ? 'text' : 'password'}`})
 }
 handleBlur = (event) => {
   switch(event.target.name){
      case 'username':
        this.setState({username:event.target.value});
        break;
      case 'password':
        this.setState({password:event.target.value});
        break;
   }
  }
  
   handleError = () => {
      this.setState({usernameError:handleUserError(this.state.username)});
      this.setState({passwordError:handleUserError(this.state.password)});
      let query = this.state.username.toLowerCase() === 'jumat' && this.state.password === 'omololu';
      this.setState({error:`${query ? '' : handleDetailMatch(this.state.username, this.state.password)}`});
   }
   
 
  render (){
    const{reveal,inputType,error,usernameError,passwordError} = this.state;
    return (
      <div className="container">
        <Header />
        <Main data ={[reveal,inputType,error,usernameError,passwordError]} onClick={this.setPassWordStatus} onBlur={(event) => this.handleBlur(event)} onSubmit={this.handleError}/>
        <Footer />
      </div>
    );
  }
}

export default App;
