import React from 'react';
import {style, style2, style3, style4, style5} from './Header.styles';
import '../../assets/font-awesome/css/font-awesome.min.css';
const Header = () => (
    <div style={style}>
        <div style={style2}>
            <i className='fa fa-users' style={style5}></i>
            <h2 style={style4}>React Login</h2>
        </div>
        <div style={style3}><i className='fa fa-bars'></i></div>
    </div>
)
export default Header;