import React from 'react';
import {style, style2, style3, style4, style5, style6, style7, style8, style9, style10, style11} from './Main.styles';
import Input from '../../components/Input/Input';
import Button from '../../components/Button/Button';
import '../../assets/font-awesome/css/font-awesome.min.css';
import './Main.css';
const Main = ({data:[reveal,type,error,usernameError,passwordError], onClick, onBlur, onSubmit}) => (
    <div style={style}>

        <div style={style2} className="login-container">

            <header style={style3}><h3>LOG IN</h3></header>
            <div style={style8}>
                <small style={style11}>{error}</small>
                <div style={style4}>

                    <div style={style7}>

                        <div style={style6} className="label"><label htmlFor ='username'>Username</label></div>

                        <div style={style5}>

                            <Input type='text' name='username' id='username' onBlur={onBlur} autoFocus={true}/>
                        </div>
                        
                    </div>
                    <small style={style11}>{usernameError}</small>
                </div>
               
                <div style={style4}>

                    <div style={style7}>

                        <div style={style6}  className="label"><label htmlFor ='password'>Password</label></div>

                        <div style={style5}>

                            <Input type={type} name='password' id='password' onBlur={onBlur} autoFocus={true}/>
                        </div>
                        <div style={style9}><i className={reveal} onClick={onClick}></i></div>
                    </div>
                    <small style={style11}>{passwordError}</small>
                </div>
               
            </div>
            <div style={style10}><Button name='submit' id='submit' text='Sign In' onClick={onSubmit}/></div>
        </div>
    </div>
)
export default Main;