import React from 'react';
import style from './Input.styles';
import './Input.css'
const Input = ({type,name,id,onBlur,autofocus}) => (
    <>
        <input className="input" style={style} type={type} name={name} id={id} onBlur={onBlur}autoFocus={autofocus}/>
    </>
)
export default Input;